// src/js/utils/poppins-font-definitions.js

// Reemplaza estas cadenas largusimas con las reales obtenidas de la conversin
const poppinsRegularBase64 = '';
const poppinsBoldBase64 = '';

export function addPoppinsFont(doc) {
    // jsPDF necesita que el prefijo 'data:font/ttf;base64,' sea eliminado si est presente
    const cleanBase64 = (base64) => base64.replace(/^data:font\/ttf;base64,/, '');

    if (!doc || typeof doc.addFileToVFS !== 'function') {
        console.error("Se necesita una instancia vlida de jsPDF con addFileToVFS.");
        return;
    }

    try {
        // Aadir los archivos de fuente virtualmente al VFS (Virtual File System) de jsPDF
        doc.addFileToVFS('Poppins-Regular.ttf', cleanBase64(poppinsRegularBase64));
        doc.addFileToVFS('Poppins-Bold.ttf', cleanBase64(poppinsBoldBase64));

        // Aadir las fuentes al documento, asocindolas con los archivos VFS
        // El tercer parmetro es el estilo ('normal', 'bold', 'italic', 'bolditalic')
        doc.addFont('Poppins-Regular.ttf', 'Poppins', 'normal');
        doc.addFont('Poppins-Bold.ttf', 'Poppins', 'bold');

        console.log("Fuentes Poppins aadidas a jsPDF.");
    } catch (error) {
        console.error("Error al aadir fuentes Poppins a jsPDF:", error);
        // Opcional: intentar usar fuente estndar como fallback
         // doc.setFont('helvetica'); // o 'times', 'courier'
    }
}